import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { TransactionInquiryComponent } from './transaction-inquiry.component';``
import { ECommerceTransactionInquiryComponent } from './ecommerce-transactions/ecommerce-transaction-inquiry.component';
const routes: Routes = [
  {
    path: '',
    component: TransactionInquiryComponent,
    data: { RouteData: new RouteData({ Title: 'transaction-inquiry' }) }
  },
  {
    path: 'ecommerce-transaction-inquiry',
    component: ECommerceTransactionInquiryComponent,
    data: { RouteData: new RouteData({ Title: 'transaction-inquiry-ecommerce-transaction-inquiry' }) }
  },
  {
    path: 'transfer-log',
    loadChildren: () => import('./transfer-log/transfer-log.module').then(m => m.TransferLogModule),
    data: { RouteData: new RouteData({ Title: 'transaction-inquiry-tansfer-log' }) }
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TransactionInquiryRoutingModule
{
}
